$font: 'PT Serif', serif;
$block: 'Open Sans', sans-serif;
$back: #fff;
$text: #3a3a3a;
$nav: #262626;
$caption: #919194;
$primary: #48a2d7;
$sec1: #2880b9;
$sec2: #e74c3c;
$link: #008080;

*{
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    margin: 0
}

a{
    color: inherit;
}

.img-container{
    text-align: center;
}
.lead-img{
    height: 450px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    @media(max-width: 600px){
        width: 100%;
        height: auto;
    }
}

.hide-desktop{
    display: none;
    @media(max-width: 700px){
        display: block;
    }
}

.hide-mobile{
    display: block;
    @media(max-width: 700px){
        display: none;
    }
}

body{ 
    font-family: $font;
    background-color: $back;
    color: $text;
    position: relative;
}

.photo-container{
    text-align: center;
    max-width: 1000px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 4em;
    img{
        width: 100%;
    }
}

.photo-caption{
    color: $caption;
    font-size: 0.8em;
    text-align: right;
    padding: 0.4em 0.8em;
    font-family: $block;
    font-weight: bold;
    max-width: 1200px;
    margin-right: 0;
    margin-left: auto;
}

.photo-caption.story-caption{  
    text-align: left;  
}

.normal-line{
    stroke: #B51514;
    stroke-width: 0.5px;
}

.copy{
    width: 91%;
    margin-left: auto;
    margin-right: auto;
    max-width: 700px;
    font-size: 1.25em;
    line-height: 1.5em;
    color: $text;
    a{
        color: $link
    }
    @media(max-width: 700px){
        padding-top: 2em;
        line-height: 1.8em;
    }
}

.copy-container{
    padding-top: 2em;
    p{
        margin-bottom: 1.5em;
    }
    @media(max-width:1300px){
        padding-top: 0.5em;
    }
} 

.blockquote .quote{
    font-size: 2.5em;
    color: $primary;
    font-family: $font;
    display: inline-block;
    margin-bottom: 0;
    width: 50px;
}

.blockquote p{
    width: 75%;
    display: inline-block;
    vertical-align: top;
}

.story{
    padding-top: 40px;
    .headline-container{
        text-align: center;
        @media(max-width: 700px){
            width: 95%;
            margin-left: auto;
            margin-right: auto;
        }
        @media(max-width: 500px){
            font-size: 0.9em;
        }
        @media(max-width: 400px){
            font-size: 0.8em;
        }
    }
    .headline{
        padding-top: 0.7em;
        font-size: 2.8em;
        margin-left: auto;
        margin-right: auto;
        max-width: 800px;
        padding-bottom: 0.7em;
        @media(max-width: 600px){
            font-size: 2.1em;
        }
    }
}

.blockquote{
  font-weight: 700;
  font-family: $block;
  font-size: 1.3em;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.5em;
  padding-top: 1em;
  padding-bottom: 1em;
}

h3.byline{
    font-weight: 500;
    margin-bottom: 0.8em;
    font-size: 1.2em;
}

.byline{
    .line{
        width: 220px;
        margin: 1em auto;
        border: 2px solid $primary;
    }
}

h3.byline.dateline{
     font-size: 1.1em;
}

.nav {
    color: white;
    height: 40px;
    background-color: $nav;
    width: 100%;
    padding: 0.2em;
    position: fixed;
    a{
        color: inherit;
    }
    .nav-logo {
        padding-left: .3em;
        vertical-align: middle;
        height: 60%;
        margin-top: 4px;
    }
    .nav-logo.mobile {
        display: none;
    }
    .nav-logo.desktop {
        vertical-align: middle;
        display: inline-block;
    }
    .right-container {
        position: absolute;
        right: 28px;
        top: 4px;
        font-size: 1.4em;
        i{
            margin-left: 8px;
        }
        @media(max-width: 750px){
            right: 14px;
        }
    }
    @media(max-width: 750px){
        .nav-logo.mobile {
            display: inline-block;
        }
        .nav-logo.desktop {
            display: none;
        }
    }
}

section{
    h2{
        padding-bottom: 0.5em;
        border-bottom: solid 4px $primary;
        margin-bottom: 0.5em;
        font-size: 1.7em;
        line-height: 1.4em;
    }
}

.credits{
    margin-bottom: 2em;
    font-size: 1em;
    font-style: italic;
}

.related-stories.copy{

    padding-top: 30px;
    max-width: 1000px;
    border-top: 1px solid #ddd;
    h2{
    border-bottom: none;
    font-size: 1.4em;
    }
    .series {
        text-transform: uppercase;
        font-size: .8em;
        padding-bottom: .1em;
        font-weight: 700;
        color: #2880b9;
    }
    img{
        width: 220px;
        @media(max-width: 700px){
            width: 90%;
        }
    }
    a{
        color: inherit;
        text-decoration: none;
        vertical-align: top;
        cursor: pointer;
    }
    .related-story{
        max-width: 210px;
        display: inline-block;
        margin-right: 2em;
        vertical-align: top;
        text-align: left;
        @media(max-width: 700px){
            margin-right: auto;
            margin-bottom: 2em;
            margin-left: auto;
            width: 90%;
            max-width: 100%;
        }
    }
    .related-story:hover{
        opacity: 0.8;
    }
    h3{
      font-size: 0.9em;
      span{
        font-style: italic;
        font-weight: bold;
      }
      @media(max-width: 700px){
        font-size: 1em;
      }
    }
    @media(max-width: 700px){
        text-align: center;
    }
}

.footer-basic-centered {
    background-color: $nav;
    color: #fff;
    width: 100%;
    text-align: center;
    font: 300 20px;
    padding: 30px;
    padding-bottom: 50px;

    margin-top: 30px;
    .footer-links{
        float: right;
    }
    .footer-company-name{
        float: left;
    }
    .footer-links,.footer-company-name{
        vertical-align: middle;
        padding-bottom: 20px;
    }
    a{
        margin-left: 5px;
        margin-right: 5px;
    }
}

@media(max-width: 700px){
    .copy{
        font-size: 1.1em;
    }
}

@media(max-width: 680px){

    .footer-basic-centered{
        padding-bottom: 30px;
        .footer-links,.footer-company-name{
            float: none;
        }
    }

}

.interactive{
text-align: center;
    .route{
        fill: none;
        stroke:#aaa;
        stroke-dasharray:5,3;
        stroke-width: 3px;
    }
    .marker{
        stroke: black;
        stroke-width: 1px;
    }
    .legend{
        text-align: center;
    }
    .year-block{
        margin-right: auto;
        margin-left: auto;
        padding: 0.6em;
        border: 1px solid; 
        border-radius: 4px;
        color: #fff;
        max-width: 300px;
        width: 95%;
        p{
            margin-bottom: 0;
                margin-left: auto;
                margin-right: auto;
        }

        @media(max-width: 450px){
            margin-right: 0.3em;
            margin-left: 0.3em;
            padding: 0.3em;
            p{
                font-size: 0.9em;
            }
        }
    }
    .year{
        font-weight: bold;
        font-size: 1.2em;

    }
    .year-block.y2012{
        background-color: #EE413B
    }
    .year-block.y2014{
        background-color: #249681
    }
    .year-block.y2016{
        background-color: #2E4D5D
    }
}

.button{
    background-color: $back;
    color: $nav;
    padding: 0.5em;
    margin: 1em auto;
    width: 140px;
    border: solid 2px $nav;
    cursor: pointer;
    border-radius: 3px;
    display: none;
    font-weight: bold;
    font-family: $block
}

.button:hover{

        background-color: $nav;
    color: white;
}

.button.show{
    display: block;
}

.route-container{
    .title{
        font-size: 1.5em;
        margin-bottom: 0.4em;
        font-weight: bold;
        color: $primary;
    }
    
    .route-dist{
        font-size: 1.2em;
        margin-bottom: 0.8em;
        font-style: italic;
    }

    .chart{
        margin: 1em 0;
    }
    .place-label{
        font-size: 0.8em;
        font-family: $block;
    }
    padding-bottom: 2em;
    border-bottom: solid 1px #eee;
    padding-top: 2em;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    .place-label.optional{
        @media(max-width: 400px){
            display: none;
        }
    }

}

.route-container:last-child{
    border-bottom: none;
}


